.section {
   &__offers {
      overflow-x: hidden !important;
      padding: 115px 0 203px;
      @media (max-width: 768px){
         padding: 115px 0;
      }
   }
}
.offers {
   &__title {
      max-width: 820px;
      width: 100%;
      margin: 0 auto 67px;
      @media (max-width: 400px){
         font-size: 30px !important;
      }
      @media (max-width: 350px){
         font-size: 26px !important;
      }
   }
   &__form {
      max-width: 848px;
      width: 100%;
      background: #ffffff;
      border: 4px solid #aaaaaa;
      border-radius: 12px;
      padding: 16px 18px;
      display: flex;
      justify-content: center;
      align-items: center;
      margin: 0 auto;
      @media (max-width: 768px){
         padding: 5px;
      }
      @media (max-width: 480px){
         flex-direction: column;
      }
   }
   &__form-input {
      width: 100%;
      height: 100%;
      border: none;
      font-family: "Montserrat";
      font-style: normal;
      font-weight: 600;
      font-size: 28px;
      line-height: 1.21;
      letter-spacing: -0.025em;
      color: #aaaaaa;
      padding: 21px;
      @media (max-width: 768px){
         font-size: 24px;
      }
      @media (max-width: 480px){
         padding: 21px 5px;
      }
   }
   &__form-btn {
      border-radius: 15px !important;
      max-width: 261px;
      width: 100%;
      border: none;
      cursor: pointer;
      @media (max-width: 768px){
         max-width: 170px;
         font-size: 18px !important;
         padding: 21px 0 !important;
      }
      @media (max-width: 480px){
         max-width: 100%;
      }
   }
}
