.section {
   &__question {
      padding: 119px 0 127px;
      background-image: url(..//img/question-bg.webp);
      @media (max-width: 480px){
         padding: 60px 0 80px;
      }
   }
}
.question {
   display: flex;
   flex-direction: column;
   align-items: flex-start;
   gap: 402px;
   @media (max-width: 1110px){
      gap: 300px;
   }
   @media (max-width: 992px){
      gap: 250px;
   }
   @media (max-width: 480px){
      gap: 150px;
   }
   &__title {
      font-family: "Montserrat";
      font-style: normal;
      font-weight: 600;
      font-size: 98.1695px;
      line-height: 1.22;
      letter-spacing: -0.025em;
      color: #ffffff;
      max-width: 493px;
      width: 100%;
      @media (max-width: 768px){
         font-size: 80px;
         max-width: 100%;
      }
      @media (max-width: 480px){
         font-size: 60px;
      }
      @media (max-width: 350px){
         font-size: 48px;
      }
   }
   &__btn {
      background: #ffffff;
      border-radius: 15px;
      padding: 21px 50px;
      transition: all .3s ease;
      &:hover{
         transition: all .3s ease;
         transform: scale(.99);
      }
      @media (max-width: 400px){
         width: 100%;
         text-align: center;
         padding: 21px 0;
      }
      p {
         background: linear-gradient(90deg, #e246ef 0%, #469eef 100%);
         -webkit-background-clip: text;
         -webkit-text-fill-color: transparent;
         background-clip: text;
         text-fill-color: transparent;
      }
   }
}
