footer{
   overflow-x: hidden !important;
}
.footer {
   display: flex;
   gap: 50px;
   justify-content: center;
   padding: 13px 0 120px;
   @media (max-width: 992px) {
      padding: 13px 0 60px;
      flex-direction: column-reverse;
      text-align: center;
   }
   &__title {
      font-family: "Montserrat";
      font-style: normal;
      font-weight: 600;
      font-size: 22px;
      line-height: 1.2;
      color: #000000;
      margin-bottom: 69px;
      @media (max-width: 992px) {
         margin-bottom: 30px;
      }
   }
   &__list {
      display: flex;
      flex-direction: column;
      gap: 40px;
      white-space: nowrap;
      @media (max-width: 992px) {
         gap: 15px;
      }
   }
   &__text {
      color: #000000 !important;
   }
   a.footer__text {
      position: relative;
      &::after {
         content: "";
         width: 100%;
         height: 2px;
         background-color: #898989;
         position: absolute;
         bottom: -1px;
         left: 0;
         transition: all 0.3s ease;
         transform: translate(-50%, 0) scaleX(0);
      }
      &:hover::after {
         transition: all 0.3s ease;
         transform: translate(0, 0) scaleX(1);
      }
   }
   &__info {
      max-width: 454px;
      width: 100%;
      @media (max-width: 992px) {
         margin: 0 auto;
      }
      .footer__text {
         color: #898989;
         margin: 24px 0;
      }
   }
   &__icons {
      display: flex;
      align-items: center;
      gap: 28px;
      a {
         transition: all 0.3s ease;
         &:hover {
            transition: all 0.3s ease;
            transform: scale(1.1);
         }
      }
      @media (max-width: 992px) {
         justify-content: center;
      }
   }
   &__logo {
      width: 100%;
      img {
         max-width: 328px;
         width: 100%;
         height: auto;
      }
   }
}
.copyright {
   padding: 20px 0;
}
.copy {
   display: flex;
   gap: 52px;
   align-items: center;
   @media (max-width: 768px){
      justify-content: center;
      gap: 20px;
   }
   @media (max-width: 480px){
      flex-direction: column;
   }
   &__text {
      font-family: "Inter" !important;
      font-weight: 400 !important;
      color: #000000 !important;
   }
}
