*,
*::before,
*::after {
	box-sizing: border-box;
}

html,
body {
	position: relative;
}

body {
	min-height: 100vh;
	height: 100%;
	display: flex;
	flex-direction: column;
	overflow-x: hidden !important;
	font-style: normal;
}

main {
	flex: 1 1 auto;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
	padding: 0;
	list-style: none;
}
a {
	text-decoration: none;
}
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
:focus-visible {
	outline: none;
}
.container {
	max-width: $mw;
	margin: 0 auto;
	width: 100%;
	@media (max-width: $md1) {
		max-width: 1024px;
	}
	@media (max-width: $md2) {
		max-width: 970px;
	}
	@media (max-width: $md3) {
		max-width: 750px;
	}
	@media (max-width: $md4) {
		padding: 0 10px;
		max-width: none;
	}
}
div,
section {
	background-position: center center;
	background-size: cover;
	background-repeat: no-repeat;
}
// main
.title {
	font-family: "Montserrat";
	font-style: normal;
	font-weight: 600;
	font-size: 48px;
	line-height: 1.4;
	letter-spacing: -0.025em;
	color: #222222;
	margin-bottom: 59px;
	text-align: center;
	@media (max-width: 480px) {
		font-size: 36px;
		margin-bottom: 40px;
	}
}
.subtitle {
	font-family: "Montserrat";
	font-style: normal;
	font-weight: 600;
	font-size: 42px;
	line-height: 1.3;
	letter-spacing: -0.025em;
	color: #ffffff;
	@media (max-width: 480px) {
		font-size: 32px;
	}
}
.text {
	font-family: "Montserrat";
	font-style: normal;
	font-weight: 500;
	font-size: 20px;
	line-height: 1.2;
	letter-spacing: -0.025em;
	color: #898989;
	@media (max-width: 480px) {
		font-size: 16px;
	}
}
.subtext {
	font-family: "Montserrat";
	font-style: normal;
	font-weight: 600;
	font-size: 24px;
	line-height: 1.2;
	letter-spacing: -0.025em;
}
.btn {
	background: #3685fb;
	border-radius: 50px;
	padding: 17px 40px;
	transition: all 0.3s ease;
	font-family: "Montserrat";
	font-style: normal;
	font-weight: 600;
	font-size: 24px;
	line-height: 1.2;
	letter-spacing: -0.025em;
	color: #ffffff;
	&:hover {
		transition: all 0.3s ease;
		background: #2763be;
	}
}
// page animation
.js-scroll {
	opacity: 0;
	transition: opacity 0ms;
	&.scrolled {
		opacity: 1;
		&.fade-in {
			animation: fade-in 1s ease-in-out 0s;
		}
		&.slide-in-left {
			animation: slide-in-left 1s ease-in-out 0s;
			@media (max-width: 480px) {
				animation: slide-in-top 0.6s ease-in-out 0s;
			}
		}
		&.slide-in-top {
			animation: slide-in-top 1s ease-in-out 0s;
		}
		&.slide-in-bottom {
			animation: slide-in-bottom 1s ease-in-out 0s;
		}
		&.slide-in-right {
			animation: slide-in-right 1s ease-in-out 0s;
			@media (max-width: 480px) {
				animation: fade-in-top 0.6s ease-in-out 0s;
			}
		}
	}
}

@keyframes slide-in-left {
	0% {
		transform: translateX(-100px);
		opacity: 0;
	}
	100% {
		transform: translateX(0);
		opacity: 1;
	}
}
@keyframes slide-in-top {
	0% {
		transform: translateY(-100px);
		opacity: 0;
	}
	100% {
		transform: translateY(0);
		opacity: 1;
	}
}
@keyframes slide-in-bottom {
	0% {
		transform: translateY(100px);
		opacity: 0;
	}
	100% {
		transform: translateY(0);
		opacity: 1;
	}
}
@keyframes slide-in-right {
	0% {
		transform: translateX(100px);
		opacity: 0;
	}
	100% {
		transform: translateX(0);
		opacity: 1;
	}
}
@keyframes fade-in {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
@media (max-width: 480px) {
	@keyframes slide-in-left {
		0% {
			transform: translateX(-50px);
			opacity: 0;
		}
		100% {
			transform: translateX(0);
			opacity: 1;
		}
	}
	@keyframes slide-in-top {
		0% {
			transform: translateY(-50px);
			opacity: 0;
		}
		100% {
			transform: translateY(0);
			opacity: 1;
		}
	}
	@keyframes slide-in-bottom {
		0% {
			transform: translateY(50px);
			opacity: 0;
		}
		100% {
			transform: translateY(0);
			opacity: 1;
		}
	}
	@keyframes slide-in-right {
		0% {
			transform: translateX(50px);
			opacity: 0;
		}
		100% {
			transform: translateX(0);
			opacity: 1;
		}
	}
}
