.section {
   &__stay {
      padding: 0 0 204px;
      overflow-x: hidden !important;
      @media (max-width: 768px){
         padding: 0 0 150px;
      }
   }
}
.stay {
   &__items {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
      gap: 40px;
      @media (max-width: 1110px){
         gap: 20px;
      }  
   }
   &__item {
      background-image: url(..//img/stay-img1.webp);
      flex: 0 1 48%;
      border-radius: 20px;
      padding: 525px 45px 61px;
      @media (max-width: 1332px){
         padding: 400px 45px 61px;
      }
      @media (max-width: 1110px){
         padding: 350px 20px 41px;
      }
      @media (max-width: 768px){
         flex: 0 1 100%;
      }
      @media (max-width: 400px){
         padding: 250px 10px 30px;
      }
      &:nth-child(2){
         background-image: url(..//img/stay-img2.webp);
      }
      &:nth-child(3){
         background-image: url(..//img/stay-img3.webp);
      }
      &:nth-child(4){
         background-image: url(..//img/stay-img4.webp);
      }
   }
}
