.section {
   &__nature {
      padding: 549px 0 219px;
      overflow-x: hidden !important;
      @media (max-width: 768px){
         padding: 449px 0 150px;
      }
   }
}
.nature {
   &__items {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 40px;
      @media (max-width: 992px){
         flex-wrap: wrap;
      }
      @media (max-width: 768px){
         max-width: 400px;
         width: 100%;
         margin: 0 auto;
      }
   }
   &__item {
      background: #bc1a6e;
      border-radius: 20px;
      cursor: pointer;
      &:hover{
         .nature__item-title{
            transform: translate(20px, 0);
            transition: all .3s ease;
         }
      }
      @media (max-width: 992px){
         flex: 0 1 47%;
         width: 100%;
      }
      @media (max-width: 768px){
         flex: 0 1 100%;
      }
      &:nth-child(2) {
         background: #cc2d4a;
      }
      &:nth-child(3) {
         background: #d93b30;
      }
   }
   &__img {
      width: 100%;
      border-radius: 20px 20px 0px 0px;
      img {
         width: 100%;
      }
   }
   &__item-title {
      padding: 50px 47px;
      transition: all .3s ease;
      @media (max-width: 1110px){
         padding: 30px 20px;
      }
      @media (max-width: 480px){
         padding: 20px 15px;
      }
   }
}
