nav {
   background: #ffffff;
   &.fixed__nav {
      position: fixed;
      left: 0;
      top: 0;
      right: 0;
      z-index: 9999;
      background: #ffffff;
      .nav {
         padding: 32px 0;
         transition: all 0.3s ease;
         @media (max-width: 992px) {
            padding: 20px 0;
         }
         @media (max-width: 480px){
            padding: 10px 0;
         }
      }
   }
}
.nav {
   display: flex;
   align-items: center;
   justify-content: space-between;
   padding: 72px 0 32px;
   transition: all 0.3s ease;
   @media (max-width: 992px) {
      position: relative;
      padding: 36px 0 20px;
   }
   @media (max-width: 480px){
      padding: 20px 0;
   }
   .nav__menu {
      @media (max-width: 992px) {
         display: none;
         &.active {
            display: flex;
            justify-content: center;
            align-items: center;
            position: fixed;
            left: 0;
            top: 0;
            right: 0;
            bottom: 0;
            width: 100%;
            height: 100vh;
            background-color: #ffffff;
            overflow-x: hidden !important;
            z-index: 99;
            animation: fade-in 0.2s ease;
         }
      }
   }
   .burger {
      @media (max-width: 992px) {
         width: 30px;
         height: 20px;
         z-index: 1001;
         position: absolute;
         right: 0;
         &::after,
         &::before {
            content: "";
            position: absolute;
            width: 100%;
            height: 3px;
            border-radius: 2px;
            left: 0;
            background-color: #3685fb;
            transition: all 0.3s ease;
         }
         &.active-burger::before {
            transform: rotate(45deg);
            top: 8.5px;
         }
         &.active-burger::after {
            transform: rotate(-45deg);
            bottom: 8px;
         }
         &::before {
            top: 0;
         }
         &::after {
            bottom: 0;
         }
         span {
            position: absolute;
            background-color: #3685fb;
            right: 0;
            width: 100%;
            height: 3px;
            top: 50%;
            transform: translate(0, -50%);
            transition: all 0.3s ease;
            border-radius: 2px;
         }
         &.active-burger span {
            transform: scale(0);
         }
      }
   }
   &__list {
      display: flex;
      align-items: center;
      gap: 36px;
      @media (max-width: 1332px) {
         gap: 15px;
      }
      @media (max-width: 992px) {
         flex-direction: column;
         gap: 25px;
      }
   }
   &__list-link {
      color: rgba(0, 0, 0, 0.76) !important;
      transition: all 0.3s ease;
      @media (max-width: 1332px) {
         font-size: 18px;
      }
      @media (max-width: 992px) {
         font-size: 24px !important;
      }
      &:hover {
         transition: all 0.3s ease;
         color: #3685fb;
      }
   }
   &__list-btn {
      background: #3685fb;
      border-radius: 50px;
      padding: 17px 40px;
      color: #ffffff !important;
      transition: all 0.3s ease;
      color: #ffffff;
      &:hover {
         transition: all 0.3s ease;
         color: #ffffff;
         background: #2763be;
      }
   }
}
