.section {
   &__hero {
      overflow-x: hidden !important;
      padding: 65px 0 52px;
      @media (max-width: 480px) {
         padding: 35px 0 52px;
      }
   }
}
.hero {
   &__body {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 84px;
      margin-bottom: 64px;
      @media (max-width: 1110px) {
         gap: 30px;
      }
      @media (max-width: 992px) {
         flex-direction: column-reverse;
      }
      @media (max-width: 480px) {
         gap: 50px;
      }
   }
   &__item {
      &:first-child {
         img {
            width: 100%;
            height: auto;
         }
      }
      &:nth-child(2) {
         max-width: 861px;
         width: 100%;
      }
   }
   &__title {
      font-family: "Montserrat";
      font-style: normal;
      font-weight: 700;
      font-size: 68.385px;
      line-height: 1.4;
      letter-spacing: -0.025em;
      color: #091f3f;
      margin-bottom: 33px;
      @media (max-width: 992px) {
         font-size: 48px;
         img {
            width: 150px;
         }
      }
      @media (max-width: 480px) {
         font-size: 36px;
         margin-bottom: 20px;
      }
   }
   &__text{
      margin-bottom: 33px;
   }
   &__form {
      background: #f0f2f5;
      border-radius: 100px;
      padding: 32px 52px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 33px;
      @media (max-width: 1332px) {
         padding: 32px;
         gap: 20px;
      }
      @media (max-width: 1110px){
         flex-wrap: wrap;
         border-radius: 40px;
      }
      @media (max-width: 768px){
         justify-content: center;
         padding: 20px;
      }
      @media (max-width: 400px){
         border-radius: 20px;
      }
      &-item {
         display: flex;
         flex-direction: column;
         width: 100%;
         border-right: 1px solid rgba(137, 137, 137, 0.5);
         padding-right: 33px;
         @media (max-width: 1332px) {
            padding-right: 20px;
         }
         @media (max-width: 1110px){
            flex: 0 1 30%;
            border-right: none;
         }
         @media (max-width: 992px){
            flex: 0 1 40%;
         }
         @media (max-width: 768px){
            flex: 0 1 48%;
         }
         @media (max-width: 480px){
            flex: 0 1 100%;
         }
      }
      .hero__text{
         margin-bottom: 0;
      }
      &-label {
         color: #000000 !important;
         font-weight: 600;
      }
      &-input {
         color: #000000 !important;
         width: 100%;
         padding: 15px 20px;
         border: none;
         &::placeholder {
            color: #898989;
         }
      }
      input[type="date"]{
         min-width: 100% !important;
         width: 100% !important;
         background: #ffffff !important;
         height: 50px !important;
         display: block;
      }
      input[type="number"] {
         min-width: 100%;
         width: 100%;
         &::-webkit-inner-spin-button {
            display: none;
         }
      }
      &-btn {
         background: #3685fb;
         max-width: 84px;
         width: 100%;
         height: 84px;
         display: flex;
         justify-content: center;
         align-items: center;
         border-radius: 50%;
         border: none;
         cursor: pointer;
         transition: all 0.3s ease;
         &:hover {
            transition: all 0.3s ease;
            background: #2763be;
         }
      }
   }
}
