.section {
   &__package {
      padding: 0 0 283px;
      overflow-x: hidden !important;
      @media (max-width: 768px){
         padding: 0 0 150px;
      }
   }
}
.package {
   &__items {
      display: flex;
      justify-content: center;
      gap: 47px;
      @media (max-width: 1110px) {
         gap: 20px;
      }
      @media (max-width: 992px) {
         flex-direction: column;
         max-width: 500px;
         margin: 0 auto;
      }
   }
   &__item {
      width: 100%;
      background: #f1f1f1;
      border: 4px solid #ffffff;
      border-radius: 20px;
      padding: 71px 34px 58px;
      opacity: 1;
      visibility: visible;
      transition: all 0.3s ease;
      @media (max-width: 1332px) {
         padding: 40px 20px;
      }
      @media (max-width: 480px) {
         padding: 20px 10px;
      }
      &:first-child {
         .package__item-list {
            margin-bottom: 145px;
            @media (max-width: 480px) {
               margin-bottom: 40px;
            }
         }
      }
   }
   &__item-title {
      font-family: "Montserrat";
      font-style: normal;
      font-weight: 600;
      font-size: 40px;
      line-height: 1.2;
      letter-spacing: -0.025em;
      color: #000000;
      margin-bottom: 93px;
      @media (max-width: 992px) {
         padding: 0 30px;
      }
      @media (max-width: 480px) {
         margin-bottom: 40px;
         padding: 0;
         font-size: 30px;
      }
   }
   &__item-list {
      margin-bottom: 61px;
      @media (max-width: 992px) {
         padding: 0 30px;
      }
      @media (max-width: 480px) {
         margin-bottom: 40px;
         padding: 0 20px;
      }
   }
   &__item-item {
      font-family: "Montserrat";
      font-style: normal;
      font-weight: 400;
      font-size: 28px;
      line-height: 1.5;
      letter-spacing: -0.025em;
      color: #000000;
      position: relative;
      margin-left: 34px;
      @media (max-width: 480px) {
         font-size: 24px;
         margin-left: 20px;
      }
      &::after {
         content: "";
         width: 0;
         height: 0;
         border-top: 11px solid transparent;
         border-left: 22px solid rgba(0, 0, 0, 0.6);
         border-bottom: 11px solid transparent;
         position: absolute;
         left: -34px;
         top: 50%;
         transform: translate(0, -50%);
         @media (max-width: 480px) {
            border-top: 7px solid transparent;
            border-left: 14px solid rgba(0, 0, 0, 0.6);
            border-bottom: 7px solid transparent;
            left: -20px;
         }
      }
   }
   &__item-btn {
      font-family: "Montserrat";
      font-style: normal;
      font-weight: 600;
      font-size: 26px;
      line-height: 1.5;
      display: flex;
      align-items: center;
      justify-content: center;
      letter-spacing: -0.025em;
      color: #ffffff;
      background: #3685fb;
      border-radius: 12px;
      padding: 28px 0;
      transition: all 0.3s ease;
      @media (max-width: 480px) {
         font-size: 22px;
      }
      &:hover {
         transition: all 0.3s ease;
         background: #ffffff;
         color: #3685fb;
      }
   }
   &__item:nth-child(3) {
      background: #a18cd1;
      border: 4px solid #ffffff;
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding: 0 60px;
      text-align: center;
      border-radius: 20px;
      @media (max-width: 1332px) {
         padding: 200px 30px;
      }
      @media (max-width: 480px) {
         padding: 150px 10px;
      }
      .package__item-title {
         color: #ffffff;
         margin-bottom: 69px;
         @media (max-width: 480px) {
            margin-bottom: 25px;
         }
      }
      .package__item-btn {
         color: #3685fb;
         background: #ffffff;
         &:hover {
            background: #3685fb;
            color: #ffffff;
         }
      }
   }
}
