.section {
   &__review {
      overflow-x: hidden !important;
      padding: 134px 0 783px;
      @media (max-width: 992px){
         padding: 134px 0 350px;
      }
   }
}
.review {
   display: flex;
   position: relative;
   @media (max-width: 992px){
      flex-direction: column;
   }
   &__header {
      max-width: 705px;
      width: 100%;
   }
   &__title {
      text-align: left !important;
      margin-bottom: 7px !important;
   }
   &__body {
      display: flex;
      justify-content: flex-end;
      gap: 20px;
      position: absolute;
      right: 0;
      @media (max-width: 1332px){
         top: 40px;
      }
      @media (max-width: 992px){
         position: relative;
      }
      @media (max-width: 768px){
         flex-wrap: wrap;
         justify-content: center;
      }
   }
   &__item {
      flex: 0 1 20%;
      width: 100%;
      @media (max-width: 992px){
         flex: 0 1 25%;
        margin-top: 0 !important;
      }
      @media (max-width: 768px){
         flex: 0 1 46%;
      }
      @media (max-width: 480px){
         flex: 0 1 100%;
      }
      &.show {
         .review__item-content {
            opacity: 1;
            visibility: visible;
            transition: all 0.3s ease;
            transform: translate(0px, 0);
            img {
               border-radius: 0px;
            }
         }
         .review__item-header {
            border: 6px solid #3685fb;
            transition: all 0.3s ease;
         }
      }
      &:nth-child(3) {
         margin-top: 100px;
      }
      &:nth-child(2) {
         margin-top: 200px;
      }
      &:nth-child(1) {
         margin-top: 300px;
      }
   }
   &__item-header {
      border-radius: 10px;
      width: 100%;
      transition: all 0.3s ease;
      height: 447px;
      @media (max-width: 1332px){
         height: 350px;
      }
      @media (max-width: 992px){
         height: auto;
      }
      @media (max-width: 480px){
         height: 350px;
      }
      @media (max-width: 320px){
         height: 300px;
      }
      img {
         width: 100%;
         height: 100%;
         object-fit: cover;
         border-radius: 10px;
      }
   }
   &__item-content {
      position: absolute;
      right: 0;
      bottom: -90px;
      opacity: 0;
      visibility: hidden;
      transition: all 0.3s ease;
      transform: translate(100px, 0);
      display: flex;
      align-items: center;
      background: #f6f4ff;
      box-shadow: 0px 20px 60px rgba(0, 0, 0, 0.12);
      border-radius: 16px;
      gap: 16px;
      padding: 33px 92px 33px 30px  ;
      @media (max-width: 1332px){
         padding: 33px 30px;
      }
      @media (max-width: 992px){
         bottom: -160px;
         width: 100%;
         justify-content: center;
      }
      @media (max-width: 480px){
         padding: 20px 10px;
      } 
      @media (max-width: 400px){
         flex-direction: column;
         bottom: -210px;
      }
      @media (max-width: 320px){
         bottom: -230px;
      }
      &::after{
         content: '';
         width: 100%;
         height: 10px;
         background: #3685FB;
         position: absolute;
         bottom: 0px;
         left: 0;
         right: 0;
         border-radius: 10px;
      }
      .review__text {
         font-family: "Inter";
         font-weight: 300;
         color: #000000;
         max-width: 450px;
         text-align: center;
         width: 100%;
      }
   }
}
